






































































































































































	export default {
		data() {
			return {
				navLists: [],
        navIndex: 0,
        tabLists: {
          '10': [
            {
              name: '待确认',
              status: 10
            },
            {
              name: '审批中',
              status: 20
            },
            {
              name: '已批卡',
              status: 30
            },
            {
              name: '未通过',
              status: 40
            },
            {
              name: '回收站',
              status: 100
            }
          ],
          '30': [
            {
              name: '已申请',
              status: 10
            },
            {
              name: '批款中',
              status: 20
            },
            {
              name: '已批款',
              status: 30
            },
            {
              name: '已拒批',
              status: 100
            },
            {
              name: '回收站',
              status: 0
            }
          ],
          '20': [
            {
              name: '未支付',
              status: 10
            },
            {
              name: '已支付',
              status: 20
            },
            {
              name: '已出单',
              status: 30
            },
            {
              name: '已退保',
              status: 100
            },
            {
              name: '回收站',
              status: 0
            }
          ],
          '40': [
            {
              name: '待核保',
              status: 10
            },
            {
              name: '待支付',
              status: 20
            },
            {
              name: '已投保',
              status: 30
            },
            {
              name: '回收站',
              status: 0
            }
          ]
        },
        activeId: 0, // 当前选中tab
        orderLists: [], // 订单数据
        orderLength: 0,
        deleteId: '',
        deleteType: 1,
        currentPage: 1, // 当前页
        maxPage: 1, // 最大页
        loading: false, // 下拉加载中
        searchStr: '', // 搜索字段
        searching: false, // 搜索状态
        inputing: false,
        insGetLinking: false, // 请求保险支付链接
        autoInsGetLinking: false // 请求车险支付链接
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      isLoad() {
        return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.orderLists.length > 0
      },
      isDelete() {
        return this.inputing && this.searchStr.length > 0
      },
      nameTips() {
        if (this.activeId === 0 || this.activeId === 4) return '浏览记录'
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      },
      showSearch() {
        return (this.orderLists.length > 0 && !this.loading) || this.searching
      },
      showNoRecord() {
        return this.orderLists.length <= 0 && !this.loading
      }
    },
		onLoad(query) {
			if (this.config.module_control_card === this.$constant.MODULE_STATE_OPEN) {
        this.$set(this.navLists, this.navLists.length, {name: '信用卡', status: 10})
      }
      if (this.config.module_control_loan === this.$constant.MODULE_STATE_OPEN) {
        this.$set(this.navLists, this.navLists.length, {name: '贷款', status: 30})
      }
      if (this.config.module_control_insurance === this.$constant.MODULE_STATE_OPEN) {
        this.$set(this.navLists, this.navLists.length, {name: '保险', status: 20})
      }
      if (this.config.module_control_insurance_car === this.$constant.MODULE_STATE_OPEN) {
        this.$set(this.navLists, this.navLists.length, {name: '车险', status: 40})
      }
      if (parseInt(query.nav) <= this.navLists.length - 1 && parseInt(query.nav) >= 0) this.navIndex = parseInt(query.nav)
      if (parseInt(query.tab) <= this.tabLists[this.navLists[this.navIndex].status].length - 1 && parseInt(query.tab) >= 0) this.activeId = parseInt(query.tab)
      this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
		},
    methods: {
      navChange: function (n) {
        if (this.navIndex === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.loading = false
        this.navIndex = n
        this.searchStr = ''
        this.searching = false
        this.orderLists = []
        this.orderLength = 0
        this.currentPage = 1
        this.maxPage = 1
        this.activeId = 0
        this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
      },
      tabChange (n, status) {
        if (this.activeId === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.loading = false
        this.activeId = n
        this.searchStr = ''
        this.searching = false
        this.orderLists = []
        this.orderLength = 0
        this.currentPage = 1
        this.maxPage = 1
        this.getOrders(this.navLists[this.navIndex].status, status)
      },
      getOrders(navStatus, status) {
        if (this.loading) return
        this.loading = true
        if (parseInt(this.currentPage) <= 1) {
          this.orderLists = []
          this.orderLength = 0
        }
        switch (navStatus) {
          case 10:
            this.$http({
              url: this.$apis.order,
              method: 'GET',
              data: {
                page: this.currentPage,
                status: status,
                keyword: this.searchStr
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) {
                    this.orderLists = []
                    this.orderLength = 0
                  }
                	this.orderLength = data.data.total
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.orderLists
                	let newArr = data.data.data
                	this.orderLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取信用卡订单失败',
                    icon: 'none'
                  })
                }
              }
            })
            break
          case 20:
            let bxOrderType = 1
            let bxOrderState = ''
            if (status === 0) {
              bxOrderType = 10
            } else {
              bxOrderState = status
            }
            this.$http({
              url: this.$apis.insuranceOrderList,
              method: 'GET',
              data: {
                page: this.currentPage,
                status: bxOrderType,
                order_status: bxOrderState,
                keyword: this.searchStr
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) {
                    this.orderLists = []
                    this.orderLength = 0
                  }
                	this.orderLength = data.data.total
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.orderLists
                	let newArr = data.data.data
                	this.orderLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取保险订单失败',
                  	icon: 'none'
                  })
                }
              }
            })
            break
          case 30:
            let dkOrderType = 1
            let dkOrderState = ''
            if (status === 0) {
              dkOrderType = 10
            } else {
              dkOrderState = status
            }
            this.$http({
              url: this.$apis.loanOrderList,
              method: 'GET',
              data: {
                page: this.currentPage,
                status: dkOrderType,
                order_status: dkOrderState,
                keyword: this.searchStr
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) {
                    this.orderLists = []
                    this.orderLength = 0
                  }
                	this.orderLength = data.data.total
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.orderLists
                	let newArr = data.data.data
                	this.orderLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取贷款订单失败',
                  	icon: 'none'
                  })
                }
              }
            })
            break
          case 40:
            let cxOrderType = 1
            let cxOrderState = ''
            if (status === 0) {
              cxOrderType = 10
            } else {
              cxOrderState = status
            }
            this.$http({
              url: this.$apis.autoInsOrderlist,
              method: 'GET',
              data: {
                page: this.currentPage,
                status: cxOrderType,
                order_status: cxOrderState,
                keyword: this.searchStr
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) {
                    this.orderLists = []
                    this.orderLength = 0
                  }
                	this.orderLength = data.data.total
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.orderLists
                	let newArr = data.data.data
                	this.orderLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取保险订单失败',
                  	icon: 'none'
                  })
                }
              }
            })
            break
        }
      },
      insGotoPay(id) {
        if (this.insGetLinking) return
        this.insGetLinking = true
        this.$http({
          url: this.$apis.insurancePay,
          method: 'GET',
          data: {
            apply_id: id
          },
          cb: (err, data) => {
            if (!err && data.success) {
              uni.setStorageSync('webSrc', data.data.url)
              this.$navigateTo({
              	url: '/pages/web/index'
              })
            } else {
              uni.showToast({
              	title: '服务器繁忙，请稍后再试',
                icon: 'none'
              })
            }
            this.insGetLinking = false
          }
        })
      },
      autoInsGotoUpload(id) {
        this.$navigateTo({
        	url: '/pages/auto/upload?bizID=' + id
        })
      },
      autoInsGotoPay(id) {
        if (this.autoInsGetLinking) return
        this.autoInsGetLinking = true
        this.$http({
          url: this.$apis.autoInsPay,
          method: 'GET',
          data: {
            order_id: id
          },
          cb: (err, data) => {
            if (!err && data.success) {
            	if (data.data.need_verify_code) {
                this.$navigateTo({
                	url: 'pages/auto/code?bizID=' + data.data.bizID + '&mobile=' + data.data.verify_mobile
                })
            	} else if (data.data.payLink) {
                uni.setStorageSync('webSrc', data.data.payLink)
                this.$navigateTo({
                	url: '/pages/web/index'
                })
            	} else {
                uni.showToast({
                	title: '订单已提交，请耐心等待核保结果',
                  icon: 'none'
                })
            	}
            } else {
              uni.showToast({
              	title: '服务器繁忙，请稍后再试',
              	icon: 'none'
              })
            }
            this.autoInsGetLinking = false
          }
        })
      },
      deleteOrder(id) {
        this.deleteType = this.activeId === 4 ? 2 : 1
        this.deleteId = id
        uni.showModal({
        	content: '您确定要删除该订单吗？',
          success: res => {
          	if (res.confirm) {
              this.deleteConfirm()
            }
          }
        })
      },
      deleteConfirm(id, type) {
        if (this.navLists[this.navIndex].status === 10) {
          this.$http({
            url: this.$apis.deleteOrder,
            method: 'DELETE',
            data: {
              id: id || this.deleteId,
              type: type || this.deleteType
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.currentPage = 1
              	this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
              } else {
                uni.showToast({
                	title: data.message,
                  icon: 'none'
                })
              }
              this.deleting = false
            }
          })
        } else if (this.navLists[this.navIndex].status === 20) {
          this.$http({
            url: this.$apis.insuranceOrderDelete,
            method: 'DELETE',
            data: {
              id: id || this.deleteId,
              type: type || this.deleteType
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.currentPage = 1
              	this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
              } else {
              	uni.showToast({
              		title: data.message,
              		icon: 'none'
              	})
              }
              this.deleting = false
            }
          })
        } else if (this.navLists[this.navIndex].status === 30) {
          this.$http({
            url: this.$apis.loanOrderDelete,
            method: 'DELETE',
            data: {
              id: id || this.deleteId,
              type: type || this.deleteType
            },
            cb: (err, data) => {
              if (!err && data.success) {
              	this.currentPage = 1
              	this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
              } else {
              	uni.showToast({
              		title: data.message,
              		icon: 'none'
              	})
              }
              this.deleting = false
            }
          })
        } else if (this.navLists[this.navIndex].status === 40) {
          this.$http({
            url: this.$apis.autoOrderDelete,
            method: 'DELETE',
            data: {
              id: id || this.deleteId,
              type: type || this.deleteType
            },
            cb: (err, data) => {
              if (!err && data.success === true) {
              	this.currentPage = 1
              	this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
              } else {
              	uni.showToast({
              		title: data.message,
              		icon: 'none'
              	})
              }
              this.deleting = false
            }
          })
        }
      },
      gotoContent(id, code, name, idcard, phone) {
        let obj = {
        	name: name,
        	idcard: idcard,
        	mobile: phone
        }
        uni.setStorageSync('queryInfo', JSON.stringify(obj))
        this.$navigateTo({
        	url: '/pages/credit/queryBank?id=' + id + '&bankCode=' + code
        })
      },
      scrollUpdate() {
        if (!this.loading && !this.isLoad && this.orderLists.length > 0) {
          this.currentPage ++
          this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
        }
      },
      search() {
        if (this.searchStr.length <= 0) {
          uni.showToast({
          	title: '请输入搜索内容',
            icon: 'none'
          })
          return
        }
        this.currentPage = 1
        this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
      },
      resetSearch() {
        this.currentPage = 1
        this.searchStr = ''
        this.searching = false
        this.getOrders(this.navLists[this.navIndex].status, this.tabLists[this.navLists[this.navIndex].status][this.activeId].status)
      }
    }
	}
